body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}


.map {
  width: 100vw;
  height: 100vh;
  position: relative;

}

.location-icon {
  font-size: 2rem;
  color:red
}

.location-info {
  position: absolute;
  top: 80px;
  right: 50px;
  width: 400px;
  min-height: 200px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  font-size: 18px;
  color: #fff;
}

.location-info ul {
  list-style-type: none;
  padding: 0;
}

.location-info li {
  padding: 5px 0;
}